import './style.scss';

const ClickableList = ( { items, onClick } ) => {
	return (
		<ul className="gutenbuild-clickable-list">
			{ items.map( ( i ) => {
                const split = i.text.split('.');

                const pills = [];

                for (let i = 0; i < split.length - 1; i++) {
                    pills.push(<span>{split[i]}</span>)       
                }


            return(
				<li key={ i.text }>
					<a href="#" onClick={ () => onClick(i) }>
                        {pills.join('')}
						{ split[ split.length - 1] }
					</a>
				</li>
			) } ) }
		</ul>
	);
};

export default ClickableList;
